import { Component, OnInit } from '@angular/core';

import { EncryptDecryptService } from '../encrypt-decrypt.service';

@Component({
  selector: 'app-md5',
  template: `
    <div>
      <div flex fxLayout="row wrap">
        <mat-form-field appearance="fill" fxFlex.gt-xs="50" fxFlex.xs="100">
          <mat-label></mat-label>
          <textarea matInput rows="6" [(ngModel)]="inputText"></textarea>
        </mat-form-field>
        <mat-form-field appearance="fill" fxFlex.gt-xs="50" fxFlex.xs="100">
          <mat-label></mat-label>
          <textarea matInput rows="6" readonly>{{outputText}}</textarea>
        </mat-form-field>
      </div>
      <mat-form-field>
        <mat-label></mat-label>
        <mat-select matNativeControl [(value)]="md5Type">
          <mat-option value="upper32">32</mat-option>
          <mat-option value="upper16">16</mat-option>
          <mat-option value="lower32">32</mat-option>
          <mat-option value="lower16">16</mat-option>
        </mat-select>
      </mat-form-field>
      <button mat-flat-button color="primary" (click)="md5Encrypt()"></button>
    </div>
  `,
  styleUrls: ['./cryptos.scss']
})
export class Md5Component implements OnInit {
  inputText: string;
  outputText: string;
  md5Type = 'upper32';

  constructor(private service: EncryptDecryptService) { }

  ngOnInit(): void {
  }

  md5Encrypt() {
    const result = this.service.md5Encrypt(this.inputText);
    switch (this.md5Type) {
      case 'upper32':
        this.outputText = result.toUpperCase();
        break;
      case 'upper16':
        this.outputText = result.toUpperCase().substring(8, 24);
        break;
      case 'lower32':
        this.outputText = result;
        break;
      case 'lower16':
        this.outputText = result.substring(8, 24);
        break;
    }
  }
}
